import React, { useState } from "react";
import styled from "styled-components";
import { IoIosCloseCircle } from "react-icons/io";


const ClosedButton = styled.div`
  position: relative;
  left: 45%;
  font-size: 28px;
  color: #000;
  cursor: pointer;
  display:inline;
`;

const Input = styled.input`
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 8px;
  margin-bottom: 8px;
  font-size: 0.9rem;
`;

const Textarea = styled.textarea`
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 8px;
  min-height: 80px;
  font-size: 0.9rem;
  resize: none;
  margin-bottom: 8px;
`;

export default function WriteModal({ModalOverlay, ModalContent, setWriteModalOpen, newComment, Button, addComments, handleCommentsChange}) {


    return (
        <ModalOverlay onClick={() => setWriteModalOpen(false)}>
          <ModalContent style={{height:"auto"}} onClick={(e) => e.stopPropagation()}>
          <ClosedButton onClick={() => setWriteModalOpen(false)}>
              <IoIosCloseCircle />
          </ClosedButton>
            <h3 style={{marginBottom: "10px"}}>   </h3>
            <Input
              id="name"
              placeholder=""
              value={newComment.name || ''}
              onChange={(e) => handleCommentsChange(e)}
            />
            <Textarea
              id="comment"
              placeholder=" .( 130)"
              value={newComment.comment || ''}
              onChange={(e) => handleCommentsChange(e)}
            />
            <Input
              id="password"
              placeholder="( 12)"
              value={newComment.password || ''}
              onChange={(e) => handleCommentsChange(e)}
            />
            <div style={{width: "100%", textAlign: "-webkit-center"}}><Button onClick={(e) => addComments(e)}></Button></div>
          </ModalContent>
        </ModalOverlay>
    );
}