import React, {useState, useEffect} from "react";
import styled from "styled-components";
import KakaoAPI from "../components/KakaoAPI";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;


const IconRow = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
`;

const IconWrapper = styled.a`
  display: flex;
  flex-direction: column;
  align-items: center;

  #naver_map {
    background: url('./img/nmap_image.jpg');
    background-size: cover;
    background-position: center;
  }
    
  #kakao_map {
    background: url('./img/kmap_image.png');
    background-size: cover;
    background-position: center;
  }
    
  #t_map {
    background: url('./img/tmap_image.png');
    background-size: cover;
    background-position: center;
  }
`;

const CircleIcon = styled.div`
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px; /*      */


`;

const IconLabel = styled.span`
  margin-top: 8px;
  font-size: 14px;
  color: #333;
`;

const MapImage = styled.img`
  flex: 1;
  width: 100%;
  object-fit: cover;
  border-top: 1px solid #ddd;
`;

export default function HowToGo({Subtitle, SubtitleKR ,Copy}) {

  const copyLocation = async () => {
      await navigator.clipboard
        .writeText("   105")
        .then(() => {
          alert(" .");
        });
      }

  return (
    <Container>
      <h2 style={{color:"black"}}>  </h2>
      <SubtitleKR>2025 10 26 ()  12</SubtitleKR>
      <h2 style={{color:"black", marginTop:"20px"}}>  </h2>
      <SubtitleKR>   9 <br/>  </SubtitleKR>
      <h2 style={{color:"black", marginTop:"20px"}}>   </h2> 
      <SubtitleKR>:    105 <Copy onClick={copyLocation} style={{height: "15px"}}/></SubtitleKR>
      
      <KakaoAPI/>
      
      <SubtitleKR>     .</SubtitleKR>
      <IconRow>
        <IconWrapper href="https://map.naver.com/p/directions/-/14110208.2799623,4509904.245778,%EB%9D%BC%EB%B9%84%EC%97%90%EB%B2%A8%EC%9B%A8%EB%94%A9,12945535,PLACE_POI/-/car?c=15.00,0,0,0,dh"
                      target="_blank">
          <CircleIcon id="naver_map"></CircleIcon>
          <IconLabel></IconLabel>
        </IconWrapper>
        <IconWrapper href="https://place.map.kakao.com/98780839" target="_blank">
          <CircleIcon id="kakao_map"></CircleIcon>
          <IconLabel></IconLabel>
        </IconWrapper>
        <IconWrapper href="https://tmap.life/6718f8fd" target="_blank">
          <CircleIcon id="t_map"></CircleIcon>
          <IconLabel></IconLabel>
        </IconWrapper>
      </IconRow>

      <MapImage
        src="./img/map.jpg"
        alt=""
      />
    </Container>
  );
}